<template>
  <div class="login">
    <div class="login__content">
      <div class="login__content-avatar">
        <i class="el-icon-s-data"></i>
      </div>
      <h3>Welcome</h3>
      <div class="login__content-group">
        <i class="el-icon-user"></i>
        <el-input type="user" placeholder="Username" v-model="user"></el-input>
      </div>
      <div class="login__content-group">
        <i class="el-icon-lock"></i>
        <el-input
          type="password"
          placeholder="Password"
          v-model="password"
        ></el-input>
      </div>
      <div class="login__content-permission">
        <el-checkbox v-model="is_checked_admin">Admin</el-checkbox>
        <el-checkbox v-model="is_checked_save">Remember password</el-checkbox>
      </div>
      <div class="login__content-submit">
        <el-button
          type="primary"
          :loading="this.loadding"
          @click.prevent="handleLogin"
          >Login</el-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import store from "@/store/index.js";

export default {
  name: "login",
  data() {
    return {
      user: "",
      password: "",
      is_checked_admin: true,
      is_checked_save: false,
      loadding: false,
    };
  },
  methods: {
    async handleLogin() {
      this.loadding = true;
      try {
        const response = await fetch(
          "https://smart-home-managerment-system-default-rtdb.firebaseio.com/account.json"
        );
        const account = await response.json();
        this.is_checked_admin ? this.checkLogin(account.admin) : this.checkLogin(account.users.user1);
      } catch (error) {
        console.log(error);
      }
    },
    inputBlank() {
      this.user = "";
      this.password = "";
    },
    checkLogin(loginInfor) {
      if (
        this.user === loginInfor.user &&
        this.password === loginInfor.password
      ) {
        this.loadding = false;
        store.state.isLoggedIn = true;
        this.$router.push({ name: "Home" });
        this.inputBlank();
      } else {
        this.inputBlank();
      }
    },
  },
  // watch: {
  //   permission() {
  //     console.log(this.permission);
  //   },
  // },
};
</script>
